.Card {
	border-radius: 20px;
	background-color: #fafafa;
	padding: 20px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.Card:hover {
	border-radius: 20px;
	box-shadow: 0 2px 6px 0 rgba(197, 186, 186, 0.5);
	background-color: #f3f3f3;
	cursor: pointer;
}

.CardTop-Cross {
	display: none;
	align-self: flex-start;
}

.Card:hover .CardTop-Cross {
	display: inline-block;
}

.Card_size_s {
	grid-column: span 2;
}

.Card_size_m {
	grid-column: span 3;
}

.Card_size_l {
	grid-column: span 4;
	grid-row: span 3;
}

.CardTop {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.CardBottom {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	margin-top: 22px;
}

.CardBottom-Corpus {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	border-radius: 20px;
	background-color: #fafafa;
	padding: 20px;
	margin-top: 20px;
}

.Card_type_critical:hover .CardBottom-Corpus {
	background-color: #f3f3f3;
	cursor: pointer;
}

