.range {
	-webkit-appearance: none;
	width: 100%;
	background: transparent;
}

.range::-webkit-slider-thumb {
	-webkit-appearance: none;
}

.range:focus {
	outline: none;
}

.range::-ms-track {
	width: 100%;
	cursor: pointer;
	background: transparent;
	border-color: transparent;
	color: transparent;
}

.range::-webkit-slider-thumb {
	-webkit-appearance: none;
	height: 20px;
	width: 12px;
	border-radius: 4.5px;
	background: #d8d8d8;
	cursor: pointer;
	margin-top: -7px;
}

.range::-moz-range-thumb {
	height: 20px;
	width: 12px;
	border-radius: 4.5px;
	background: #d8d8d8;
	cursor: pointer;
}

.range::-ms-thumb {
	height: 20px;
	width: 12px;
